<template>
    <div>
        <div class="row mx-0">
            <div class="col-sm-12 col-md-3 px-0">
                <Header
                :name="user.name"
                />
                <div class="center pb-4">
                    <b-btn v-b-modal.modal-logout variant="outline-secondary" class="logout">Cerrar Sesin</b-btn>
                    <b-modal id="modal-logout" centered hide-footer hide-header 
                    body-class="p-0" dialog-class="px-5" content-class='border-0'>
                        <div class="d-block text-center">
                            <img :src="closeIcon" class="icon3 pt-5"><br>
                            <p class="modalHeader pt-3">Cerrar sesin</p>
                            <p class="modalBody">Ests seguro que deseas salir?</p>
                        </div>
                        <div class="wrapper center">
                            <b-button class="one mt-3 py-3 btnleft" @click="$bvModal.hide('modal-logout')">Cancelar</b-button>
                            <b-button class="two mt-3 btnright" @click="logout()">Confirmar</b-button>
                        </div>
                    </b-modal>
                </div>

                <SideMenu/>
            </div>
            <div class="col-sm-12 col-md-9 bg-gris">
                <b-row class="py-4 px-5">
                    <b-col cols="12" md="6">
                        <h2 class="blueText">{{cityName}}</h2>
                    </b-col>
                    <b-col cols="12" md="6">
                        <div class="btn-toolbar">
                            <CSV 
                            :reporte="reportInfo"
                            />                                
                            <Dropdown
                            :departments="dep.deptos"
                            />     
                        </div>                                          
                    </b-col>
                </b-row>
                <hr>
                <b-row>
                    <b-col cols="12" lg="7" >
                        <div v-if="isLoading" class="center pt-5">
                            <div class="spinner-border text-primary" role="status">
                                <span class="sr-only">Loading...</span>
                            </div>
                        </div>
                        <FormInfoBase v-else
                        :equipo="bd.equipo_convertido"
                        :extra="bd.$_extra_m3"
                        :diasS="bd.dias_sucios"
                        :base0="bd.base0"
                        :bono="depto.bono_persona"
                        :asistencia="depto.asistencia"
                        />
                    </b-col>
                    <b-col cols="12" lg="5" class="px-0">
                        <Graphics/>
                    </b-col>
                </b-row>
                
            </div>
        </div>
        
    </div>
</template>


<script>
import Header from '../components/Header';
import SideMenu from '../components/SideMenu';
import FormInfoBase from '../components/FormInfoBase';
import Graphics from '../components/Graphics';
import Dropdown from '../components/Dropdown';
import CSV from '../components/CSV';
import i_logout from '../assets/iconocerrar.png'

export default {
    name: 'Dashboard',
    components: {
        Header,
        SideMenu,
        FormInfoBase,
        Graphics,
        Dropdown,
        CSV,
    },
    data() {
        return {
            closeIcon: i_logout,
        }
    },
    async beforeMount() {
        let token = await this.$store.dispatch("userService/getToken");
        this.$store.dispatch("userService/decode", token);
    }, 
    created() {
        this.$store.dispatch("plants/getBDInfo", {// info del model (Bd)
            department: 'almacen',
            city: 'cancun'
        });
        this.$store.dispatch("plants/getAllInfo", {// info del dashboard general -> /calculator 
            department: 'almacen',
            city: 'cancun'
        });    
        this.$store.dispatch('admin/getInfo', 'Cancn'); //trae los nombres de departamenots de la planta
        this.$store.dispatch('admin/getReportsInfo');
    },
    computed: {
        isLoading() {
            return this.$store.getters['plants/loading'];
        },
        depto() {
            return this.$store.getters['plants/data'];
        },
        bd() {
            return this.$store.getters['plants/bd'];
        },
        cityName() {
            return this.$store.getters["admin/getName"];
        },
        depName() {
            return this.$store.getters["admin/getDep"];
        },
        dep(){
            return this.$store.getters['admin/data'];
        },
        reportInfo(){
            return this.$store.getters['admin/report']
        },
        user() {            
            return this.$store.getters['userService/getIdentity'];
        },
    },
    methods: {
        logout: function(){
            this.$router.push({ name: "login"});
            this.$store.dispatch("userService/logout");
        }
    },

}
</script>
